import { Component, OnInit } from '@angular/core';
import { TimelineMax } from 'gsap'

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss'],
})
export class HomeComponent implements OnInit {
  menu = new TimelineMax({ defaults: { ease: "power1.out" } });
  constructor() { }

  ngOnInit(): void {
    this.createMenuAnim()
  }
  name = "Varshith Raj "
  surname = "Basa"
  about_me = "I completed my graduation in computer science and engineering at Mallareddy college of engineering and technology, Hyderabad. I passionate about UI development."

  public createMenuAnim() {
    this.menu.to(".text", { y: "5%", duration: 1.5, stagger: 0.5 });
    this.menu.to(".intro", { y: "-100%", duration: 1 });
    this.menu.to(".intro2", { y: "-100%", duration: 1.25 }, '-=1');
    this.menu.to(".home-text", { x: "0%", duration: 1 });
    this.menu.to(".varshith-min", { x: "0%", duration: 1 }, '-=1');
  }
  public downloadCV() {
    window.open("https://varshithrajbasa.github.io/varshithrajbasa-resume.pdf");
  }
}
